// new development
const url1 = "https://raw.githubusercontent.com/japmelian/datasets/main/dataset-alquiler.csv";


d3.csv(url1, (err, rows) => {
    function unpack(rows, key) {
        return rows.map(function(row) {
            return row[key]
        })
    };
  
    var data = [
        {
            type: "scattermapbox", lon: unpack(rows, "lon"), lat: unpack(rows, "lat"), zipcode: unpack(rows, "zipcode"), price: unpack(rows, "price"), text: unpack(rows, "price"),
            marker: { size: 5, color: unpack(rows, "price"), colorscale: "YlOrRd", showscale: true }
          }
    ];
  
    var layout = {
      mapbox: { style: "dark", zoom: 5, center: { lon: -3.70361, lat: 40.41694 } },
      margin: { r: 0, t: 0, b: 0, l: 0 }
    };
  
    var config = {
      mapboxAccessToken: "pk.eyJ1IjoiamFwbWVsaWFuIiwiYSI6ImNsOGx4ZmoyNjBhbmszd3A0bGptZ3R3eWIifQ.Fg5ABrsWM17O1nY4xwTxWw"
    };
  
    Plotly.newPlot('map', data, layout, config);
  });